let oDiv = null // svgdom
let vbCX = 0
let vbCY = 0
let vbCW = 0
let vbCH = 0
let zoomVal = 0 // 
let zoomStepSize = 20 // 20
let removeFlag = false // 
let startX = 0 // x
let startY = 0 // y
let moveX = 0 // 
let moveY = 0 // 
let endX = 0 // x
let endY = 0 // y
let zoomW = 41 //  > zoomStepSize * 2 
let zoomH = 41 //  > zoomStepSize * 2 
let zoomX = 0
let zoomY = 0

// SVG
module.exports.Init = function (svgId) {
    oDiv = document.getElementById(svgId).firstChild
    getCurrentVB() // svg
    // DOMMouseScroll
    addEvent(oDiv, 'mousewheel', onMouseWheel)
    addEvent(oDiv, 'DOMMouseScroll', onMouseWheel)
    oDiv.addEventListener('mousedown', moveDownMouse, false)
    oDiv.addEventListener('mouseup', moveUpMouse, false)
    oDiv.addEventListener('mousemove', moveMouse, false)
}

// 
function onMouseWheel(e) {
    let ev = e || window.event
    let down = true
    // ev.wheelDeltaev.wheelDelta < 0truefalse
    // ev.wheelDeltaev.detail > 0truefalse
    // wheelDeltadetailwheelDeltadetail
    down = ev.wheelDelta ? ev.wheelDelta < 0 : ev.detail > 0
    if (down) {
        zoomOut() // 
    } else {
        zoomIn() // 
    }
    if (ev.preventDefault) {
        ev.preventDefault()
    }
    return false
}
function zoomIn() {
    if (zoomW > zoomStepSize * 2 && zoomH > zoomStepSize * 2) {
        zoomVal += zoomStepSize
        zoomTo('in')
    }
}
function zoomOut() {
    zoomVal -= zoomStepSize
    if (zoomVal >= -zoomStepSize * 11) {
        zoomTo('out')
    } else {
        zoomVal = -zoomStepSize * 11 // -220
    }
}
function zoomTo(flag) {
    // viewBox
    getCurrentVB()
    // ***********   ***********
    if (flag === 'in') {
        zoomX = vbCX + zoomStepSize
        zoomY = vbCY + zoomStepSize
        zoomW = vbCW - zoomStepSize * 2
        zoomH = vbCH - zoomStepSize * 2
    } else {
        zoomX = vbCX - zoomStepSize
        zoomY = vbCY - zoomStepSize
        zoomW = vbCW + zoomStepSize * 2
        zoomH = vbCH + zoomStepSize * 2
    }
    // ***********   ***********
    // viewBox
    oDiv.setAttributeNS(null, 'viewBox', zoomX + ' ' + zoomY + ' ' + zoomW + ' ' + zoomH)
    // viewBox  vbCX vbCY vbCW vbCHviewBox
    endZoom()
}
function endZoom() {
    getCurrentVB()
    // (x, y)
    endX = vbCX
    endY = vbCY
}

// 
function moveDownMouse(evt) {
    removeFlag = true
    startX = parseInt(evt.clientX) // 
    startY = parseInt(evt.clientY) // 
}
// 
function moveMouse(evt) {
    if (removeFlag) {
        oDiv.setAttributeNS(null, 'style', 'cursor: move')
        moveX = parseInt(evt.clientX) - startX // -=
        moveY = parseInt(evt.clientY) - startY // -=
        vbCX = endX - moveX
        vbCY = endY - moveY
        // viewBox
        oDiv.setAttributeNS(null, 'viewBox', vbCX + ' ' + vbCY + ' ' + vbCW + ' ' + vbCH)
    }
}
// 
function moveUpMouse(evt) {
    oDiv.setAttributeNS(null, 'style', 'cursor: default')
    // (x, y)
    endX = vbCX
    endY = vbCY
    removeFlag = false
}

// VB
function getCurrentVB() {
    // console.log(oDiv.clientWidth)
    vbCX = parseFloat(oDiv.viewBox.animVal.x)
    vbCY = parseFloat(oDiv.viewBox.animVal.y)
    vbCW = parseFloat(oDiv.viewBox.animVal.width)
    vbCH = parseFloat(oDiv.viewBox.animVal.height)
}
// dom
function addEvent(obj, xEvent, fn) {
    if (obj.attachEvent) {
        obj.attachEvent('on' + xEvent, fn)
    } else {
        obj.addEventListener(xEvent, fn, false)
    }
}